import { ref } from "vue";
import { projectAuth } from "./fbconnect";
import { storage } from "./handlers";
import * as K from "../constants/constants";

const outError = ref(null);

const logout = async() => {
    outError.value = null;
    try {
        await projectAuth.signOut();
        await storage.clear();
    } catch (err) {
        K.error(err.message);
        outError.value = err.message;
    };
};

const useLogout = () => {
    return { logout, outError };
};

export default useLogout