body {
  overscroll-behavior-y: contain;
}

.infoText {
  color: white;
}

.dateText {
  font-size: 1rem;
}

.headerText {
  font-size: 1.25rem;
}

.recordHeaderText {
  font-size: 1rem;
  max-width: 75vw;
}

.header-icon {
  position: absolute;
  bottom: 0;
  left: 44%;
  transform: translateX(-50%);
}

p {
  font-size: 1rem;
}

body.scan {
  background: black;
}

