import {
    createRouter,
    createWebHistory,
} from 'vue-router'
import Home from '../views/Home.vue'
import Scan from '../views/Scan.vue'
import History from '../views/History.vue'
import Inbox from '../views/Inbox.vue'
import Settings from '../views/Settings.vue'
import Show from '../views/Show.vue'
import QRCode from '../views/QRCode.vue'
import Insiders from '../views/Insiders.vue'
import ProductList from '../views/ProductList.vue'

import * as K from "../constants/constants";


export const routes = [{
        path: '/',
        name: 'Home',
        meta: { desc: "Home" },
        component: Home
    },
    {
        path: '/scan',
        name: 'Scan',
        meta: { desc: "Scan the QR Code", backBtn: true },
        component: Scan,
    },
    {
        path: '/history',
        name: 'History',
        meta: { desc: "Records" },
        component: History,
    },
    {
        path: '/insiders',
        name: 'Insiders',
        meta: { desc: "Insiders" },
        component: Insiders,
    },
    {
        path: '/productlist',
        name: 'ProductList',
        meta: { desc: "ProductList", backBtn: true },
        component: ProductList,
        props: true,
    },
    {
        path: '/inbox',
        name: 'Inbox',
        meta: { desc: "Inbox" },
        component: Inbox,
    },
    {
        path: '/show',
        name: 'Show',
        meta: { desc: "Show", backBtn: true },
        component: Show,
        props: true,
    },
    {
        path: '/settings',
        name: 'Settings',
        meta: { desc: "Settings" },
        component: Settings,
    },
    {
        path: '/qrcode',
        name: 'QRCode',
        meta: { desc: "QRCode", backBtn: true },
        component: QRCode,
    },
    {
        path: "/:pathMatch(.*)*",
        name: 'Home',
        meta: { desc: "" },
        component: Home
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router