import { ref } from 'vue';
import { projectAuth } from './fbconnect';
import * as K from "../constants/constants";

const user = ref(projectAuth.currentUser);

projectAuth.onAuthStateChanged((_user) => {
    if (K.devMode && process.env.NODE_ENV != 'production') {
        K.print(`User state changed. Current user is: ${JSON.stringify(_user)}`);
    };
    user.value = _user;
})

const getUser = () => {
    return { user };
};

export default getUser;