import { projectAuth, gprovider } from "./fbconnect";
import * as K from "../constants/constants";
import { ref } from "vue";


const error = ref(null);

const login = async() => {
    try {
        let result = await projectAuth.signInWithPopup(gprovider);
        // let result = await projectAuth.signInWithRedirect(gprovider);
        error.value = null;
        return true;
    } catch (err) {
        var errorMessage = err.message;
        error.value = 'Login invalid';
        K.error(errorMessage);
        return false;
    };
};

const useLogin = () => {
    return { error, login };
};

export default useLogin;