<template>
  <div class="q-pa-md row justify-between">
    <div class="column">
      <div class="boldText">Template by Random Event</div>
    </div>
    <div>
      <span>Version {{ K.version }}</span>
    </div>
  </div>
  <q-separator />
  <div class="q-pa-md row justify-between items-center">
    <div class="column">
      <div class="boldText">Update App</div>
    </div>

    <div>
      <q-icon name="refresh" color="black" style="font-size: 1.5rem" @click="updateApp" />
    </div>
  </div>

  <div class="column justify-center">
    <q-list>
      <q-separator />

      <q-item>
        <!-- <div @click="addKey"> -->
        <q-item-section>
          <q-item-label>
            <span class="boldText">Add API Key</span>
          </q-item-label>
          <q-item-label caption lines="2"></q-item-label>
        </q-item-section>
        <q-item-section side>
          <q-icon @click="addKey" name="arrow_forward_ios" color="black" />
        </q-item-section>
        <!-- </div> -->

      </q-item>

      <q-separator />

    </q-list>
  </div>
  <div class="q-pa-sm row items-center justify-center">
    <UserAuth />
  </div>
</template>

<script>
import { ref } from "vue";
import * as K from "../constants/constants";
import { dialogHandler, storage } from "../composables/handlers";
import { useRouter } from "vue-router";
import UserAuth from "../components/UserAuth.vue";

export default {
  components: { UserAuth },
  setup() {
    const updateDate = ref("");
    const router = useRouter();

    const loadData = async () => {

    };

    const addKey = async () => {
      dialogHandler({
        title: "Add API Key",
        message: `Please add the API Key you like.`,
        html: true,
        prompt: {
          model: "",
          type: "text",
        },
        persistent: true,
        ok: {
          label: "OK",
          style: { "text-transform": "none" },
          color: "green",
        },
        cancel: {
          label: "Cancel",
          style: { "text-transform": "none" },
        },
      })
        .onOk(async (inputApi) => {
          // save to local api key
          storage.set('api', inputApi);
          router.replace({ name: "Insiders" });
        })
        .onCancel(() => {

        });
    };

    loadData();

    const updateApp = () => {
      window.location.reload(true);
      updateDate.value = "";
    };
    return {
      updateApp,
      updateDate,
      addKey,
      K,
    };
  },
};
</script>

