import { GetProduct, GetProductList } from "./connection";
import { storage } from "./handlers";
import * as K from "../constants/constants";

const fetch = async(theme_name, fn) => {
    let data = {};
    try {
        let prodList = await fn(theme_name);
        const dt = Date.now();
        data = { dt, prodList }
        if (theme_name != 'watching') {
            await storage.set(theme_name, data);
        };
        return prodList
    } catch (err) {
        return []
    };
};

export const handleGetData = async(id = 'themes', fn = GetProduct) => {
    let data = JSON.parse(await storage.get(id));
    let content;
    try {
        if (!data.dt) {
            content = await fetch(id, fn).data;
        } else if ((Date.now() - Date.parse(data.dt)) / 1000 / 60 / 60 > 24) {
            content = await fetch(id, fn).data;
        } else if (data.prodList.length == 0) {
            content = await fetch(id, fn).data;
        } else {
            content = data.prodList;
        };
    } catch (err) {
        content = await fetch(id, fn);
    };
    return content;
};