<template>
  <div v-if="prodList.length == 0" class="row items-center justify-center" style="height: 90vh">
    <div class="headerText">No Product List</div>
  </div>
  <div v-else class="q-px-none q-pb-sm row items-start q-gutter-md">
    <q-list>
      <q-item v-for="item in prodList">
        <q-card class="my-card">
          <q-img :src="item.image">
            <div class="absolute-bottom text-h6" @click="addToFav(item)">
              {{ item.fav ? '*' : '-' }} {{ item._id }} - {{ item.selling[0] }} - {{ item.status }}
            </div>
          </q-img>

          <q-card-section>
            {{ item.name }}

          </q-card-section>

        </q-card>
      </q-item>
    </q-list>
  </div>
</template>
  
<script>
import { GetProduct, GetProductList, updateProductFav } from "../composables/connection";
import { handleGetData } from "../composables/dataHandler";
import { ref } from "vue";
import * as K from "../constants/constants";

export default {
  components: {},
  props: ["itemName"],
  setup(props) {
    const prodList = ref([]);
    (async () => {
      try {
        // prodList.value = await GetProductList(props.itemName);
        const content = await handleGetData(props.itemName, GetProductList);
        prodList.value = content.map(e => {
          let prodData = e.data;
          prodData['fav'] = e.watching;
          return prodData
        });
        prodList.value = prodList.value.filter(
          e => { return e._id.slice(0, 3) != '500' }
        );
      } catch (err) { K.error(err) };
    })();

    const addToFav = async (item) => {
      prodList.value = await Promise.all(prodList.value.map(async (e) => {
        if (e._id == item._id) {
          e.fav = !e.fav;
          await updateProductFav(item._id, item.fav);
          return e
        } else {
          return e
        };
      }));
    };

    return {
      prodList,
      addToFav
    };
  },
};
</script>
  
<style scoped>
.headerText {
  font-size: 1rem;
  font-weight: bold;
}

.my-card {
  width: 100%;
  width: calc(90vw);
  max-width: 700px;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.boldText {
  font-weight: bold;
  font-size: 1rem;
}
</style>