<template>
  <div v-if="noRecord" class="row items-center justify-center" style="height: 90vh">
    <div class="headerText">No Record</div>
  </div>
  <div v-else>
    <q-list>
      <q-item>
        <q-item-section>
          <q-item-label>
          </q-item-label>
        </q-item-section>
      </q-item>

      <q-separator />
    </q-list>
  </div>
</template>

<script>
import { ref } from "vue";
import { io } from 'socket.io-client';
import * as K from "../constants/constants";

export default {
  components: {},
  setup() {
    const socket = io('https://pc-socket.onrender.com');
    socket.on('new-update', (data) => {
      K.print(`update view: ${data}`);
    });
    const noRecord = ref(true);
    return { noRecord, socket };
  },
  beforeUnmount() {
    this.socket.disconnect();
  },
  beforeRouteLeave: async function () {
    this.socket.disconnect();
  },
};
</script>

<style scoped>
.boldText {
  font-weight: bold;
}

.headerText {
  font-size: 1rem;
  font-weight: bold;
}
</style>