<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import { clearCamera } from "../composables/handlers";
import * as K from "../constants/constants";

export default {
  components: {},
  emits: ["reload"],
  setup() {
    const router = useRouter();

    clearCamera();

    const toScan = () => {
      clearCamera();
      router.replace({ name: "Scan" });
    };

    const pressedScan = () => {
      clearCamera();
      toScan();
    };

    const pressedQR = () => {
      clearCamera();
      router.replace({ name: "QRCode" });
    };

    const pressedCode = () => {
      clearCamera();
      K.print("pressed code");
    };

    return {
      toScan,
      router,
      pressedScan,
      pressedQR,
      pressedCode,
    };
  },
  beforeCreate: function () {
    document.body.className = "home";
  },
  beforeUnmount: function () {
    document.body.className = "normal";
  },
};
</script>

