<template>
  <div v-if="!user" class="row items-center justify-center" style="height: 90vh">
    <div class="headerText">No Data</div>
  </div>
  <div v-else>
    <div v-if="themes.length == 0" class="row items-center justify-center" style="height: 90vh">
      <div class="headerText">No Data</div>
    </div>
    <div v-else class="column justify-center">
      <q-list>
        <q-separator />
        <q-item v-for="item in themes">
          <q-item-section>
            <q-item-label>
              <span class="boldText">{{ item.name }}</span>
            </q-item-label>
            <q-item-label caption lines="2"></q-item-label>
          </q-item-section>
          <q-item-section side>
            <q-icon @click="toProducts(item)" name="arrow_forward_ios" color="black" />
          </q-item-section>
        </q-item>
        <q-separator />
      </q-list>
    </div>
  </div>
</template>
  
<script>
import { GetProduct, GetProductList } from "../composables/connection";
import { handleGetData } from "../composables/dataHandler";
import { ref } from "vue";
import * as K from "../constants/constants";
import { useRouter } from "vue-router";
import UserAuth from "../components/UserAuth.vue";
import getUser from "../composables/getUser";

export default {
  components: { UserAuth },
  setup() {
    const router = useRouter();
    const { user } = getUser();
    const themes = ref([]);
    const toProducts = async (item) => {
      await router.replace({
        name: "ProductList",
        params: { itemName: item.name },
      });
    };
    (async () => {
      try {
        let products = await handleGetData('themes', GetProduct);
        themes.value = products.data;
        const watching = {link: 'watching', name: 'watching'}
        themes.value.unshift(watching)
      } catch { };

    })();
    return {
      themes,
      toProducts,
      user
    };
  },
};
</script>
  
<style scoped>
.headerText {
  font-size: 1rem;
  font-weight: bold;
}

.boldText {
  font-weight: bold;
  font-size: 1rem;
}
</style>