import { storage } from "./handlers";
import { projectFirestore } from "./fbconnect";
const K = require('../constants/constants');

export const dbName = 'insiders';
export const baseurl = 'https://www.lego.com/';


export const GetProductList = async(theme_name = 'city') => {
    let prodList = [];
    try {
        let querySnapshot;
        if (theme_name != 'watching') {
            querySnapshot = await projectFirestore.collection('insiders').where("data.theme", "==", theme_name).get();
        } else {
            querySnapshot = await projectFirestore.collection('insiders').where("watching", "==", true).get();
        };
        querySnapshot.forEach(doc => {
            const data = doc.data();
            prodList.push(data);
        });
        return prodList;
    } catch (err) {
        K.error(err);
        return [];
    };
};

export const GetProduct = async(id = 'themes') => {
    try {
        let mdata = [];
        const snapshot = await projectFirestore.collection("insiders").doc(id).get()
        if (snapshot.exists) {
            mdata = snapshot.data();
            K.print(mdata);
        } else {
            mdata = [];
            K.print('no data');
        };
        var data = await storage.get(id);
        if (data == null) {
            await storage.set(id, mdata);
        };
        data = mdata;
        return data;
    } catch (err) {
        K.error(err);
        return []
    };
};

export const updateProductFav = async(key, status) => {
    try {
        const docRef = await projectFirestore.collection("insiders").doc(key);
        await docRef.update({
            watching: status
        });
        let prodInfo = await GetProduct(key);
        await storage.set(key, prodInfo);
    } catch (err) {
        K.error(err);
        return [];
    };
};