<template>
  <div v-if="isLoaded" class="enclose row fullScreenSize items-center justify-between">
    <div class="fullWidth column items-center justify-center">
      <div class="row items-center justify-center q-mb-none q-pb-none">
        <p class="infoText q-ma-sm q-pa-sm" @click="copy(dataMeta)">
          {{ dataMeta }}
        </p>
      </div>
    </div>
  </div>
  <div class="row items-center justify-center q-mt-none q-pt-none">
    <AppButton text="Home" :action="homePressed" />
  </div>
</template>

<script>
import { ref } from "vue";
import QRcode from "../components/QRcode.vue";
import AppButton from "../components/AppButton.vue";
import { copyToClipboard, notifyHandler } from "../composables/handlers";
import { sdata } from "../constants/constants";
import { useRouter } from "vue-router";

export default {
  components: { QRcode, AppButton },
  props: ["decoded"],
  setup(props) {
    const openPage = ref(true);
    const flashSequence = ref(0);

    const dataMeta = ref(sdata);
    const router = useRouter();

    const isLoaded = ref(false);

    const copy = async (val) => {
      copyToClipboard(val);
      notifyHandler({
        message: 'Copied',
        color: 'green',
        html: true,
      });
    };

    (async () => {
      isLoaded.value = true;
    })();

    try {
      if (props.decoded) {
        dataMeta.value = props.decoded.replace(/&quot;/g, '"');
      }
    } catch {
      dataMeta.value = sdata;
    }

    const closePressed = async () => {
      openPage.value = false;
      await router.replace({ name: "Scan" });
    };

    const homePressed = async () => {
      await router.replace({ name: "Home" });
    };

    return {
      dataMeta,
      closePressed,
      homePressed,
      isLoaded,
      flashSequence,
      copy,
    };
  },


  beforeRouteLeave: async function (to, from, next) {
    this.openPage = false;
    next();
  },
};
</script>

<style scoped>
.boldText {
  font-weight: bold;
}

.infoText {
  word-wrap: anywhere;
  color: black;
}

.headerText {
  font-size: 1rem;
  font-weight: bold;
}
</style>